<template>
    <div class="container">
        <div class="overflow-auto" style="max-height: 800px;">
            <ListElement v-for="item in list" :key="item.id" :author="item" />
        </div>
    </div>
  </template>
  
<script>
    import ListElement from './Author-List-Element.vue'

    export default {
        name: 'Authors-Page',
        data() {
            return {
                list: []
            };
        },
        components: {
            ListElement
        },
        mounted(){
            this.fetchData();
        },
        methods:{
            async fetchData(){
                try {
                    const response = await fetch('https://blog.kreosoft.space/api/author/list', {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    });

                    if (!response.ok) {
                    throw new Error('Ошибка HTTP: ' + response.status);
                    }

                    const data = await response.json();

                    this.list = data;
                
                } catch (error) {
                    console.error('Ошибка входа:', error);
                }
            },
        }
    }
</script>